body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:rgba(0, 0, 0, 0) -webkit-linear-gradient(left, rgb(15, 189, 240), rgb(164, 6, 249)) repeat scroll 0% 0%;
  background:rgba(0, 0, 0, 0) linear-gradient(to right, rgb(15, 189, 240), rgb(164, 6, 249)) repeat scroll 0% 0%;
}

